
body {
  background: url('./img/back.jpg');
  background-size: cover;
  overflow: hidden;
}

.App {
  text-align: center;
}

.App h1 {
  padding-top: 3rem;
  font-size: 3rem;
  letter-spacing: 1px;
  color: rgb(248, 242, 242);
}

.openModal {
  padding: 0.5rem 1rem;
  width: 10rem;
  color: rgb(253, 197, 144);
  border-radius: 10px;
  border: none;
  background: rgb(134, 6, 128);
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 20px;
  box-shadow:0px 0px 1rem rgb(134, 133, 133) ;
  transition: all .4s ease;
}

.openModal:hover {
  box-shadow:0px 0px 2rem black ;
  transform: scale(1.04);
  background: rgb(184, 8, 175);
  color: black;
}
