.modalContainer {
  margin: auto;
  width: 60vw;
  height: 70vh;
  background: lightgray;
  position: relative;
  border-radius: 20px;
  box-shadow: 0px 0px 1rem rgb(100, 100, 100);
  margin-bottom: 3rem;
  margin-top: 7rem;
  transition: all .3s ease;
}


.modalContainer:hover {
    box-shadow: 0px 0px 2rem rgb(160, 158, 158);
    transform: scale(1.015);
}

.modalContainer p {
  padding-top: 10rem;
  font-size: 20px;
  font-family: sans-serif;
  width: 80%;
  text-align: center;
  margin: auto;
  letter-spacing: 2px;
  padding-bottom: 10rem;
}

.closeImage {
  width: 2rem;
  height: 2rem;
  position: absolute;
  right: 2rem;
  top: 2rem;
  padding: .5rem;
  border-radius: 50%;
  border: 3px solid rgb(255, 3, 3);
  transition: all .4s ease;
}

.closeImage:hover {
    box-shadow: 0 0 1rem red;
}
.agreeButton {
  position: absolute;
  bottom: 5rem;
  left: 10rem;
  width: 20rem;
  background: crimson;
  padding: 0 5rem;
  border: none;
  border-radius: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
  height: 3rem;
  text-align: center;
  font-size: 1.5rem;
  color: white;
  transition: all .4s ease;
}

.agreeButton:hover {
    box-shadow: 0 0 1rem red;
    background: rgb(230, 47, 84);
}
.rejectButton {
  position: absolute;
  bottom: 5rem;
  right: 10rem;
  width: 20rem;
  background: rgb(20, 67, 220);
  padding: 0 5rem;
  border: none;
  border-radius: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
  height: 3rem;
  text-align: center;
  font-size: 1.5rem;
  color: white;
  transition: all .4s ease;
}

.rejectButton:hover {
    box-shadow: 0 0 1rem rgb(20, 67, 220);
    background: rgb(64, 104, 238);
}
